* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background: #f1f5f8;
  color: #222;
}

.book-list {
  width: 90vw;
  max-width: 1170px;
  margin: 5rem auto;
  display: grid;
  gap: 2rem;
}

.book img {
  width: 100%;
  object-fit: cover;
}

.book {
  background-color: #fff;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
  position: relative;
}

.book h2 {
  margin-top: 1rem;
  font-size: 1rem;
}

.book h4 {
  color: #617d98;
  font-size: 0.75rem;
  margin-top: 0.5rem;
  letter-spacing: 2px;
}
.book-list button {
  padding: 20px;
  border: none;
  margin: 20px;
  outline: none;
  background-color: aquamarine;
  cursor: pointer;
  border-radius: 10px;
}
.number {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.75rem;
  font-size: 1rem;
  border-top-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  background-color: #c35600;
  color: #fff;
  font-weight: bold;
}
h1 {
  text-align: center;
  margin-top: 4rem;
  text-transform: capitalize;
}

@media screen and (min-width:768px) {
  .book-list {
    grid-template-columns: repeat(3, 1fr);
  }
}